<template>
<div>
    <div class="p-3 border-bottom border-2">
        <span class="h2">
            Timedule
        </span>
    </div>
    <div class="text-center">
        <div :key="formKey">
            <img src="@/assets/login.png" height="150" class="my-3">
            <div class="form-group m-3 col-md-4 mx-md-auto">
                <label for="email"></label>
                <input type="email" class="form-control" id="email" placeholder="" v-model="username" @keydown.enter="focusTo('password')">
            </div>
            <div class="form-group m-3 col-md-4 mx-md-auto">
                <label for="password"></label>
                <input :type="pwType" class="form-control" id="password" placeholder="" v-model="password" @keydown.enter="logIn()">
            </div>
            <div class="form-check d-inline-block mb-3">
                <input class="form-check-input" type="checkbox" id="showPass" v-model="showPass">
                <label class="form-check-label" for="showPass"></label>
            </div>
        </div>
        <div class="btn btn-outline-primary" @click="logIn()"></div>
        <div class="m-3">
            <small>
                  <router-link to="/signup"></router-link>
            </small>
        </div>
        <div class="m-3">
            <small>
                  <a class="link" @click="showResetPassModal"></a>
            </small>
        </div>
        <transition>
            <div v-if="errcode" class="alert alert-danger m-3" role="alert">
                : {{ this.errcode }}
            </div>
        </transition>
        <transition>
            <div v-if="emailSended" class="alert alert-success m-3" role="alert">
                
            </div>
        </transition>
    </div>
    <b-modal v-model="resetPassModal" title="" hide-header-close @ok="sendResetEmail">
        <div class="form-group text-center">
            <label for="email"></label>
            <input type="email" class="form-control" id="email" placeholder="" v-model="username" @keydown.enter="sendResetEmail">
        </div>
    </b-modal>
</div>
</template>

<style>
.v-leave-active {
    transition: opacity 1s
}
.v-leave-to {
    opacity: 0;
}
</style>

<script>
import firebase from 'firebase'

export default {
  name: 'Login',
  data: () => ({
    formKey: 0,
    username: '',
    password: '',
    showPass: false,
    resetPassModal: false,
    emailSended: false,
    errcode: '',
  }),
  mounted() {
    firebase.auth().onAuthStateChanged((user)=> {
      if (user !== null) {
        this.$router.replace('/user/' + user.uid);
      }
    });
  },
  methods: {
    focusTo(id) {
      document.getElementById(id).focus();
    },
    logIn() {
      firebase.auth().signInWithEmailAndPassword(this.username, this.password)
        .then((userCredential) => {
          let user = userCredential.user;
          this.$router.replace('/user/' + user.uid);
        })
        .catch((error) => {
          this.errcode = error.code;
          this.formKey += 1;
          setTimeout(() => {
            this.errcode = '';
          }, 2000);
        });
    },
    showResetPassModal() {
      this.resetPassModal = true;
    },
    sendResetEmail() {
      firebase.auth().sendPasswordResetEmail(this.username)
        .then(() => {
          this.emailSended = true;
          setTimeout(() => {
            this.emailSended = false;
          }, 2000);
        })
        .catch((error) => {
          this.errcode = error.code;
          this.formKey += 1;
          setTimeout(() => {
            this.errcode = '';
          }, 2000);
        });
    },
  },
  computed: {
    pwType: function () {
      if (this.showPass) {
        return 'text';
      } else {
        return 'password';
      }
    },
  },
}
</script>
